
class StorageBase{
	constructor(storage){
		this.storage=storage;
	}
	/*
	@key string 
	@getOne boolean 
	@retrun Object 
	getOne==true 
	*/
	getJson(key,getOne){
		var data =JSON.parse(this.storage.getItem(key));
		if(getOne===true)
			this.removeItem(key);
		return data;
	}
	/*
	@key string 
	@data object 
	*/
	extendJson(key,data){
		var oldData =this.getJson(key);
		Object.assign(oldData,data);
		this.setJson(key,oldData);
	}
	/*
	@key string 
	@data object 
	*/
	setJson(key,data){
		return this.storage.setItem(key,JSON.stringify(data));
	}
	/*
	@key string 
	*/
	removeItem(key){
		return this.storage.removeItem(key);
	}

	/*
	@key string 
	@data string 
	*/
	setItem(key,data){
		return this.storage.setItem(key,data);
	}

	/*
	@key string 
	@retrun srting 
	*/
	getItem(key,getOne){
		var data =this.storage.getItem(key);
		if(getOne===true)
			this.removeItem(key);
		return data;
	}

}
export const sessionStorage=new StorageBase(window.sessionStorage);
export const localStorage=new StorageBase(window.localStorage);


